<template>
  <div class="landing">
    <!-- top banner -->
    <v-container id="top-jumbo">
      <v-row justify="space-around">
        <v-col class="content">
          <div class="display-3 font-weight-bold mb-3">
            Welcome to <span class="red--text">Portal</span> <br />
          </div>
            <h2>Canidate Selection made easy!</h2>
          <div class="text--disabled subtitle-1 mb-5">
            Lorem, ipsum dolor sit amet consectetur adipisicing elit. Non
            adipisci quibusdam minus dolores quasi, quos recusandae.
          </div>
          <v-btn large class="font-weight-bold mt-5" link to="/login" color="error">Login</v-btn>
        </v-col>
        <v-col class="d-none d-lg-block">
          <v-img
            max-width="500"
            lazy-src="https://via.placeholder.com/1000"
            :src="require('@/assets/landing-bg.jpg')"
          ></v-img>
        </v-col>
      </v-row>
    </v-container>
    <!-- end top banner -->
    <!-- register section -->
    <div class="red lighten-4" id="register-user">
      <v-container >
        <v-row>
          <v-col
            ><v-card class="mx-auto my-12" max-width="374">
              <v-img height="250" lazy-src="https://via.placeholder.com/1000" src="https://images.pexels.com/photos/2173508/pexels-photo-2173508.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940"></v-img>
              <v-card-text>
                <v-text-field
                  label="Full Name"
                  v-model="name"
                ></v-text-field>
                <v-text-field
                  label="Proctor ID"
                  :rules="id_rules"
                  prefix="ID-"
                  v-model="proctor_id"
                  counter="8"
                ></v-text-field>
                <v-text-field
                  label="Email Address"
                  v-model="email"
                  :rules="email_rules"
                ></v-text-field>
              </v-card-text>
              <v-card-actions>
                <v-btn block class="font-weight-bold mb-3" color="error" @click="register">
                  Register
                </v-btn>
              </v-card-actions>
            </v-card></v-col
          >
          <v-col class="content-2 mt-5">
            <div class="display-1 font-weight-bold mb-3">The most useful resource <br /> <span class="red--text"> ever created for Proctors.</span></div>
