          </v-col>
        </v-row>
      </v-container>
    </div>
  </div>
</template>

<script>
import AuthDataService from '@/services/AuthDataService'

export default {
  name: "landing",
  data: () => ({
    email_rules: [
      (value) => !!value || "Required!",
      (value) => {
        const pattern =
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return pattern.test(value) || "Invalid e-mail!";
      },
      // (value) => {
      //   const pattern = /^(?!.+@(temple)\..+)(.+@.+\..+)$/;
      //   return pattern.test(value) || "Must be a Temple e-mail!"
      // }
    ],
    id_rules: [
      (value) => !!value || 'This field is required',
      (value) => (value || "").length <= 8 || 'Proctor ID must be less than 8 characters',
    ],
    email: null,
    proctor_id: null,
    timeout: 2500,
    name: null
  }),
  methods: {
    register(){
      AuthDataService.register(this.name, this.email, this.proctor_id).then(() => {
        window.alert("Success")
        this.$router.push({ name: "login" }) 
      }).catch((err) => {
        console.error(err.message)
      })
    }
  },
};
</script>

